import styled from 'styled-components';

export const TabList = styled.ul`
    border-bottom: 1px solid #ccc;
    padding-left: 0;
    list-style-type: none;
    text-align: center;
`;

export const TabContent = styled.div `
    padding-top: 1rem;
    padding-bottom: 1rem;
`


